  <template>
  <div class="header-bar">
    <h1 class="title">
      <router-link :to="{ name: 'HomeView'}">
        NLP Dataset Explorer
      </router-link>
    </h1>
    <ul v-if="showOptions" class="nav-options">
      <template v-for="d in datasets">
        <li @click="onLoadOption"><router-link :to="{ name: 'HomeView', query: { dataset: d }}">{{d}}</router-link></li>
      </template>
    </ul>

    <ul v-if="!showOptions" class="nav">
      <li>{{dataset}}</li>
    </ul>

    <ul v-if="!showOptions" class="nav-more">
      <li @click="onShowOptions">More</li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'header-bar',
  data () {
    return {
      datasets: ['WNLI', 'QNLI', 'QQP', 'RTE', 'SST-2', 'STS-B', 'DIAGNOSTICS', 'DPR', 'CMRC', 'DRCD'],
      showOptions: false
    }
  },
  computed: {
    // Getting Vuex State from store/modules/activities
    ...mapState({
      dataset: state => state.docs.dataset
    })
  },
  methods: {
    ...mapActions([
      'loadDataset'
    ]),
    showTalion: function () {
      this.$emit('showTalion')
    },
    onShowOptions: function () {
      this.showOptions = true
    },
    onLoadOption: function (d) {
      this.showOptions = false
    }
  }
}
</script>

<style lang="scss" scoped>
@media (max-width: 768px) {
  .title {
    font-size: 105%;
  }
}
.header-bar {
  .title {
    flex: 1;
    // max-width: 4.6rem;
    // max-height: 2.2rem;
    font-family: optima;
    margin-right: 1.8rem;
    // font-size: 0;
    line-height: 3.2rem;
    word-break: break-all;

    a {
      display: block;
    }
  }
  .nav-options {
    display: block;
    justify-content: flex-end;
    text-align: right;
    position: absolute;
    right: 0rem;
    top: 0rem;
    background: white;
    padding: 5px 14px;
    border: 1px solid #eee;
    li {
      display: block;
      padding: 1rem 0;
    }
  }
  .nav-more {
    justify-content: flex-end;
  }
  ul {
    display: flex;
    flex: 1;
    justify-content: flex-start;
  }

  li {
    display: inline-block;
    font-size: 1.6rem;
    margin-right: 1.2rem;
    cursor: pointer;
  }

  .talion {
    font-size: 0;
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
    width: 2.4rem;
    height: 1.8rem;
    margin-top: 0.4rem;
  }
}
</style>



